@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

body {
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(140deg, #673ab7, #3f51b5);
}

.container {
    padding: 1rem 0;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    background-color: #fff;
}

button:active {
    -webkit-transform: scale(0.98);
            transform: scale(0.98);
}

#root {
    min-width: 60rem;
    /* margin: 1rem auto; */
}


