.input-bar {
    padding: 0.25rem;
    background-color: #f1f1f1;
    display: flex;
    flex-direction: row;
    width: 15rem;
    justify-content: space-around;
    position: relative;
}

.input-section {
    margin: 0.5rem auto;
    border: 1px dashed #898989;
}

input[type="color"] {
    width: 100%;
}

@media screen and (max-width: 650px) {
    input {
        width: 100%;
    }
    .control-form {
        border: none;
        width: 100%;
        background: #000000;
        color: #fff;
    }
    .input-section {
        width: 100%;
    }
    .input-bar {
        width: auto;
        background-color: inherit;
        grid-column-gap: 0.5rem;
        -webkit-column-gap: 0.5rem;
                column-gap: 0.5rem;
    }
}
.preview-box {
    padding: 2rem;
    width: 100%;
    height: 30rem;
    display: grid;
    place-items: center;
    overflow: hidden;
}

.demo-box {}
.code-section {
    padding: 2rem;
    background-color: rgb(0, 0, 0);
    color: #fff;
    position: relative;
}

.copy-btn {
    cursor: pointer;
    outline: none;
    border: none;
    font-weight: 500;
    padding: 0.5rem;
    color: #fff;
    background-color: blueviolet;
    text-align: center;
    width: 100%;
    margin-top: 1rem;
    transition: 0.3s background ease;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    grid-column-gap: 1rem;
    -webkit-column-gap: 1rem;
            column-gap: 1rem;
}

.copy-btn:hover {
    background-color: rgb(97, 11, 177);
}

.copy-btn:active {
    background-color: rgb(65, 65, 65);
}

code {
    font-family: monospace, Arial, Helvetica, sans-serif;
    transition: all 0.25s ease-in-out;
}

code::selection {
    background-color: #dedede;
    color: #000;
}

.code-snippet {
    background: #272727;
    padding: 0.5rem 1rem;
    color: #cdcdcd;
}

.clipboard-icon {
    width: 1rem;
    height: 1rem;
}

.feedback-message {
    position: absolute;
    top: 0;
    right: 0;
    padding: 0.5rem 0.75rem;
    font-weight: 500;
    letter-spacing: 1px;
    background: #00b788;
}

@media screen and (max-width: 550px) {
    .code-section {
        padding: 0;
    }
    .copy-btn {
        margin-top: 0;
    }
    .code-snippet {
        padding: 1rem;
    }
}
.developer {
    position: fixed;
    padding: 0.5rem 1rem;
    background: #1c1c1c;
    bottom: 1rem;
    right: 1rem;
    text-decoration: none;
    color: #fff;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    grid-column-gap: 0.75rem;
    grid-column-gap: 0.75rem;
    -webkit-column-gap: 0.75rem;
            column-gap: 0.75rem;
    font-size: 1rem;
}

.github-icon {
    background: #7388ff;
    border-radius: 50%;
    width: 1.25rem;
    height: 1.25rem;
}

.developer:hover {
    background-color: #000;
    transition: 0.3s all ease-in-out;
    border-radius: 0.5rem;
    box-shadow: 0 5px 0px -1px #ff0000;
}
